import React, { Component } from 'react';

class ViewCollection extends Component {
    render() {
        return (
            <div className="card" style={{
                    maxWidth: '100%',
                    position: 'absolute',
                    left: '5%',
                    right: '5%',
                    margin: 'auto',
                    top: '20%',
                    transform: 'translateY(-50%)'
                }}>
                <p>video</p>
              

            </div>
        );
    }
}

export default ViewCollection;


